<template>
    <div class="manage">
        <el-dialog title="提示" :visible.sync="dialogVisible" width="50%">
            <!-- 用户表单验证 -->
            <el-form
                ref="form"
                :rules="rules"
                :inline="true"
                :model="form"
                label-width="80px"
                :before-close="handleClose"
            >
                <el-form-item label="姓名" prop="name">
                    <el-input
                        placeholder="请输入姓名"
                        v-model="form.name"
                    ></el-input>
                </el-form-item>
                <el-form-item label="年龄" prop="age">
                    <el-input
                        placeholder="请输入年龄"
                        v-model="form.age"
                    ></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-select v-model="form.sex" placeholder="请选择性别">
                        <el-option label="男" :value="1"></el-option>
                        <el-option label="女" :value="0"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="出生日期" prop="birth">
                    <el-date-picker
                        v-model="form.birth"
                        type="date"
                        placeholder="选择日期"
                        value-format="yyyy-MM-DD"
                    >
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="地址" prop="addr">
                    <el-input
                        placeholder="请输入地址"
                        v-model="form.addr"
                    ></el-input>
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>
        <div class="manage-header">
            <el-button @click="handleAdd" type="primary"> +新增 </el-button>
            <!-- form搜索区 -->
            <el-form :inline="true" :model="userForm">
                <el-form-item
                    ><el-input
                        placeholder="请输入名称"
                        v-model="userForm.name"
                    ></el-input
                ></el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="onSubmit">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
          <!-- 表格tab -->
        <div class="common-tabel">
          
            <el-table :data="tableData" style="width: 100%" height="90%" stripe>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="sex" label="性别">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{
                            scope.row.sex == 1 ? "男" : "女"
                        }}</span>
                    </template></el-table-column
                >
                <el-table-column prop="age" label="年龄"></el-table-column>
                <el-table-column
                    prop="birth"
                    label="出生日期"
                ></el-table-column>
                <el-table-column prop="addr" label="地址"></el-table-column>
                <el-table-column prop="addr" label="操作">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="handleEdit(scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            size="mini"
                            @click="handleDel(scope.row)"
                            type="danger"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>
            <div class="pager">
                <el-pagination
                    layout="prev, pager, next"
                    :total="total"
                    @current-change="pageChange"
                >
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import { getUser, addUser, delUser, editUser } from "../api";
export default {
    data() {
        return {
            dialogVisible: false,
            form: {
                name: "",
                age: "",
                sex: "",
                birth: "",
                addr: "",
            },
            rules: {
                name: [
                    {
                        required: true,
                        message: "请输入姓名",
                        trigger: "blur",
                    },
                ],
                age: [
                    {
                        required: true,
                        message: "请输入年龄",
                        trigger: "blur",
                    },
                ],
                sex: [
                    {
                        required: true,
                        message: "请选择性别",
                        trigger: "blur",
                    },
                ],
                birth: [
                    {
                        required: true,
                        message: "请选择出生日期",
                        trigger: "blur",
                    },
                ],
                addr: [
                    {
                        required: true,
                        message: "请输入地址",
                        trigger: "blur",
                    },
                ],
            },
            tableData: [],
            modelType: 0, //0表示新增 1表示修改
            total: 0, //页码
            pageData: {
                page: 1,
                limit: 10,
            },
            userForm: {
                name: "",
            },
        };
    },
    methods: {
        //新增确定按钮
        submit() {
            this.$refs.form.validate((valid) => {
                //console.log(valid);
                if (valid) {
                    if (this.modelType === 0) {
                        addUser(this.form).then(() => {
                            //重新获取列表
                            this.getList();
                        });
                    } else {
                        editUser(this.form).then(() => {
                            this.getList();
                        });
                    }
                    console.log(this.form);
                    //清空表单数据清空
                    this.$refs.form.resetFields();
                    //关闭弹窗
                    this.dialogVisible = false;
                }
            });
        },

        handleClose() {
            this.$refs.form.resetFields();
            this.dialogVisible = false;
        },
        cancel() {
            this.handleClose();
        },
        handleEdit(row) {
            //console.log(row);
            this.modelType = 1;
            this.dialogVisible = true;
            //
            this.form = JSON.parse(JSON.stringify(row));
        },
        handleDel(row) {
            this.$confirm("是否删除该用户?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            })
                .then(() => {
                    delUser({ id: row.id }).then(() => {
                        this.$message({
                            type: "success",
                            message: "删除成功!",
                        });
                        this.getList();
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消删除",
                    });
                });
        },
        getList() {
            //获取列表数据
            getUser({ params: { ...this.pageData, ...this.userForm } }).then(
                ({ data }) => {
                    //console.log(data);
                    console.log(1);
                    this.tableData = data.list;
                    this.total = data.count || 0;
                }
            );
        },
        //新增用户
        handleAdd() {
            this.modelType = 0;
            this.dialogVisible = true;
        },
        //选择页码的回调函数
        pageChange(val) {
            console.log(val);
            this.pageData.page = val;
            this.getList();
        },
        //列表搜索条件
        onSubmit() {
            this.getList();
        },
    },
    mounted() {
        this.getList();
    },
};
</script>
<style scoped lang="less">
.manage {
    height: 90%;
    .manage-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .common-tabel {
        position: relative;
        height: calc(100% - 62px);
        .pager {
            position: absolute;
            bottom: 0;
            right: 20px;
            
        }
    }
}
</style>